import React, { useEffect } from "react";
import { useHistory } from "react-router-dom";
import { Card, Table, Container, Row } from "reactstrap";
import Title from "../../title/Title";
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { fetchLeaves, updateStatus } from "../../../../redux/actions";
import { TheHeaderDropdownTasks } from "../../../../containers";
import Swal from "sweetalert2";

const LeaveHistory = () => {
  const state = useSelector(state => state);
  const dispatch = useDispatch();
  const history = useHistory();
  const { leaves: leaveRequests, leave_updated } = state.leaves;

  const handleRequestDetails = e => {
    e.preventDefault();
    const id = e.target.classList[0];
    return history.push(`/admin/employee/details/${id}`);
  };

  useEffect(() => {
    dispatch(fetchLeaves());
  }, [leave_updated, dispatch]);

  const handleStatusUpdate = async e => {
    e.preventDefault();
    const id = e.target.classList[0];

    Swal.fire({
      html: `
      <form class="row">
      <h3 class="py-3">Update Leave Request Status</h3>
    <select id="update-status" class="form-control form-control-lg">
      <option value="">--Choose a Status--</option>
      <option value="APPROVED">APPROVED</option>
      <option value="REJECTED">REJECTED</option>
      <option value="PENDING">PENDING</option>
    </select>
    </form>
      `,
      confirmButtonText: "Confirm",
      showCancelButton: true,
      preConfirm: function() {
        return new Promise((resolve, reject) => {
          resolve({
            status: document.getElementById("update-status").value
          });
        });
      }
    }).then(async ({ value }) => {
      const response = await dispatch(updateStatus(id, value));
      response.status
        ? Swal.fire({
            text: "Update Successful",
            icon: "success",
            button: false,
            timer: 2000
          })
        : Swal.fire({
            text: "Error updating Status",
            icon: "error",
            timer: 1500
          });
    });
  };

  const handleDelete = () => {};

  if (!leaveRequests) return null;

  return (
    <>
      <div>
        <div className="pb-5 pt-5 pt-md-8">
          <Container fluid style={{ width: "97%" }}>
            {!leaveRequests && (
              <div style={{ height: "70vh" }}>
                <div className="bg-white">
                  <div
                    className="d-flex flex-column justify-content-center align-items-center text-dark"
                    style={{ height: "30vh", fontSize: "17px" }}>
                    <p className="pb-0 mb-1 font-weight-bold">You don't have any leave requests for now.</p>
                  </div>
                </div>
              </div>
            )}
            {leaveRequests.length > 0 && (
              <>
                <Title titleClass="title text-white" title="Leave Requests" />
                <Row>
                  <div className="col">
                    <Card className="shadow p-0">
                      <Table className="align-items-center table-flush" responsive>
                        <thead className="thead-light">
                          <tr>
                            <th scope="col">Staff ID</th>
                            <th scope="col">Name</th>
                            <th scope="col">Type</th>
                            <th scope="col">Starts</th>
                            <th scope="col">Ends</th>
                            <th scope="col">Duration</th>
                            <th scope="col">Relieve Staff</th>
                            <th scope="col">Status</th>
                            <th scope="col" />
                          </tr>
                        </thead>

                        <tbody>
                          {leaveRequests.map((entry, index) => {
                            const {
                              id,
                              staff: { firstname, lastname, staff_id },
                              type,
                              leave_start,
                              leave_end,
                              duration,
                              relieve_staff,
                              status
                            } = entry;
                            return (
                              <tr key={index}>
                                <th scope="row">
                                  <Link to={`/employees/${staff_id}`} style={{ color: "black" }}>
                                    <div className="pl-3">
                                      <span className="mb-0 text-sm d-inline-block text-truncate" style={{ maxWidth: "150px" }}>
                                        {staff_id}
                                      </span>
                                    </div>
                                  </Link>
                                </th>
                                <td>{`${firstname} ${lastname}`}</td>
                                <td>{type}</td>
                                <td>
                                  <div>{new Date(leave_start).toDateString()}</div>
                                </td>
                                <td>
                                  <div>{new Date(leave_end).toDateString()}</div>
                                </td>
                                <td>
                                  <div>{duration}</div>
                                </td>
                                <td>
                                  <div>{`${relieve_staff.firstname} ${relieve_staff.lastname}`}</div>
                                </td>
                                <td>
                                  <div>{status}</div>
                                </td>
                                <td className="text-right">
                                  <span>
                                    <TheHeaderDropdownTasks
                                      // detailsKey={"View Details"}
                                      className={id}
                                      displayDetails={handleRequestDetails}
                                      handleDelete={handleDelete}
                                      deleteKey={"Delete Request"}
                                      updateKey={"Update Status"}
                                      statusUpdate={handleStatusUpdate}
                                    />
                                  </span>
                                </td>
                              </tr>
                            );
                          })}
                        </tbody>
                      </Table>
                    </Card>
                  </div>
                </Row>
              </>
            )}
          </Container>
        </div>
      </div>
    </>
  );
};

export default LeaveHistory;
